.container {
    width: 85%;
    margin: 0 auto; }

.radio {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    align-items: center;
    min-height: 100vh;
    padding: 125px 0;

    &__header,
    &__player-wrap {
        width: 100vw;
        position: fixed;
        z-index: 100;
        left: 0;
        background-color: $darkgrey; }

    &__header {
        top: 0;
        box-shadow: 0px 2px 8px 0px rgba(0,0,0,0.75); }

    &__player-wrap {
        bottom: 0;
        box-shadow: 0px -2px 8px 0px rgba(0,0,0,0.75);
        opacity: 0;
        transform: translateY(100%);
        visibility: hidden;
        transition: transform .3s ease, opacity .3s ease;

        &_active {
            visibility: visible;
            opacity: 1;
            transform: translateY(0); } }

    &__recent,
    &__all {
        width: 100%; }

    &__recent {
        transition: transform .3s ease, opacity .3s ease;

        &_hidden {
            transform: scaleY(0);
            opacity: 0; } }


    &__title {
        width: 100%;
        color: $white;
        padding: 20px 0; }

    &__wrap {
        max-width: 2000px;
        width: 100%;
        display: grid;
        justify-content: space-between;
        grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
        grid-gap: 2vw;
        padding: 0 20px;

        &_latest {
            grid-template-columns: repeat(auto-fill, minmax(120px, 1fr)); } } }

.load {
    color: #fff;
    white-space: nowrap;
    border: 2px solid $orange;
    background-color: $orange;
    line-height: 40px;
    font-size: 20px;
    padding: 0 20px;
    margin-top: 50px;
    transition: color .3s ease, background-color .3s ease;

    &:hover {
        color: $orange;
        background-color: transparent; } }

.logo {
    min-height: 100%;
    width: 150px;
    display: flex;
    align-items: center;
    padding: 20px 0;

    &__link {
        display: block; }

    &__img {
        display: block;
        width: 100%; } }
